body {
  min-width: 320px;
  -webkit-user-select: none;
  user-select: none;
  background-color: #fff;
  margin: 0;
  padding: 20px;
  position: relative;
}

.drag-area, .draggable-item, .dropped-item {
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  border: 1px solid #000;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.title {
  text-align: center;
  font: 16px Arial, sans-serif;
}

.draggable-item {
  z-index: 1;
}

.draggable-item_hidden {
  display: none;
}

.draggable-item, .dropped-item_canvas {
  position: absolute;
}

.container {
  column-gap: 20px;
  margin-top: 20px;
  display: flex;
}

.drop-area {
  box-sizing: border-box;
  width: calc(50% - 10px);
  aspect-ratio: 1 / 1;
  border: 1px dashed #000;
  overflow: auto;
}

.drop-area_highlighted {
  background-color: #cfe8fc;
}

.drop-area_grid {
  flex-wrap: wrap;
  align-content: flex-start;
  gap: 5px;
  padding: 5px;
  display: flex;
}

.drop-area_canvas {
  position: relative;
}

